.students-list-marks {
    margin-top: 10px;
    min-height: 100px;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(500px, 1fr));
    grid-gap: 20px;
}

.student-profile-marks {
    margin-top: 20px;
    width: 500px;
    height: 250px;
    display: flex;
    flex-direction: row;
    padding-bottom: 10px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.4);
    background-color: white;
}

.student-profile-marks-left {
    width: 250px;
    height: 250px;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-right: 1px solid black;
}

.student-profile-marks-right {
    align-self: center;
    padding-left: 10px;
}

.student-profile-marks-text {
    margin-top: 5px;
}

.update-btn {
    width: 80px;
    height: 30px;
    margin-top: 10px;
    margin-right: 10px;
    background-color: #3f51b5;
    color: #fff;
    border: none;
    border-radius: 4px;
    font-size: 16px;
    cursor: pointer;
}

.marks-input {
    width: 50px;
}